.centered {
  display: flex;
  justify-content: center;
}
.photoNumber {
  display: flex;
  flex-direction: column;
  padding-bottom: 10px;
}
.photoNumber div {
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
}
.photoNumber div p {
  padding: 15px 30px;
  background: #ccc;
  font-size: 48px;
  border-bottom-left-radius: 6px;
  border-bottom-right-radius: 6px;
}
